import React, {useState} from "react";
import  s from'./ProfileInfo.module.css'

export const ProfileiStatus = (props: { status: string }) => {

    const [editmode, setEditmode] = useState(true)
    const [status, setStatus] = useState('')


    const ediMode=()=>{setEditmode(false)}
    return (



        <div className={s.status} onDoubleClick={ediMode}>

            {
              editmode?
                <div>
                    <span  defaultValue={'status'} onDoubleClick={ediMode}>{status}</span>
                </div>:
                <div>
                    <input onBlur={()=>{setEditmode(true)}}
                           value={status}
                    onChange={(e)=>{setStatus(e.currentTarget.value)}}/>

                </div>
            }


        </div>

    )
}